import { Pipe, PipeTransform } from '@angular/core';
import * as constants from '../../config/locales/en.json';

@Pipe({
  name: 'constantsarray'
})
export class ConstantsArrayPipe implements PipeTransform {

  transform(key: any, args?: string): any {

    const keys = key.split('.');
    let constant = constants[keys[0]], valueObj = {};
    const value = [];

    if (constant) {

      for (let i = 1; i < keys.length; i++) {

        constant = constant[keys[i]];
        if (constant === undefined) {
          break;
        }
      }

      if (constant !== undefined) {
        valueObj = constant;
      }
    }

    for (const val in valueObj) {
      if (valueObj.hasOwnProperty(val)) {
        value.push({
          key: val,
          value: valueObj[val]
        });
      }
    }

    return value;
  }
}
